* {
  margin: 0;
  padding: 0
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

img {
  border: 0;
  vertical-align: bottom;
}

/* header */
.header {
  width: 100%;
  height: 120px;
  border: 1px solid #eee;
  border-top: none;
  box-sizing: border-box;
  position: fixed;
  background: #fff;
  z-index: 100;
}

.header_contents {
  width: 100%;
  height: 100%;
  max-width: 1400px;
  position: relative;
  margin: 0 auto;
}

/* logo */
.logo {
  margin: 0px 20px;
}

/* form_container */
.form_container {
  position: relative;
  width: 350px;
  top: 10px;
  left: 50%;
  margin-left: -175px
}

.search_input {
  width: 300px;
  height: 10px;
  border-radius: 20px;
  padding: 15px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  outline: none;
}

.search_input:focus {
  outline: 1px solid orange;
}

.search_btn {
  width: 25px;
  height: 25px;
  background: url("../public/images/header/seach_btn_icon.png") no-repeat center/cover;
  border: 0;
  position: absolute;
  right: 55px;
  top: 3px;
  cursor: pointer;
}

.search_btn:hover {
  background: url("../public/images/header/seach_btn_o_icon.png") no-repeat center/cover;
}

/* header ul, li */
.header ul {
  width: 500px;
  position: absolute;
  bottom: 10px;
  left: 150px;
  display: flex;
  justify-content: space-evenly;
}

.category_icon {}

.category_icon a {
  display: block;
  text-align: center;
  color: #999;
  font-size: 13px;
  font-weight: bold;
}

.category_icon a:after {
  content: "";
  display: block;
  width: 0%;
  height: 2px;
  background: linear-gradient(45deg, rgb(238, 229, 144), rgb(255, 145, 0));
  margin-top: 5px;
  border-radius: 5px;
  transition: all 0.5s;
}

.category_icon a:hover {
  color: #000;
}

.category_icon a:hover:after {
  width: 100%;
}

.category_icon img {
  width: 24px;
  height: 24px;
  display: block;
  margin: auto;
  padding-bottom: 5px;
}

/* user_menu */
.user_menu {
  width: 70px;
  padding: 5px;
  border-radius: 20px;
  position: absolute;
  right: 100px;
  top: 20px;
  border: 1px solid #ddd;
}

.user_icon {
  width: 30px;
  height: 30px;
  background: url("../public/images/header/user_icon.png") no-repeat center/cover;
  transition: all 0.3s;
  cursor: pointer;
  float: right;
}

.user_icon:hover {
  background: url("../public/images/header/user_icon_o.png") no-repeat center/cover;
}

.lang {
  width: 25px;
  height: 25px;
  background: url("../public/images/header/lang_icon.png") no-repeat center/cover;
  transition: all 0.3s;
  cursor: pointer;
  float: left;
  margin-top: 3px;
  position: relative;
}

.lang:hover {
  background: url("../public/images/header/lang_icon_o.png") no-repeat center/cover;
}


/* category_box */
.category_box {
  width: 100%;
  min-height: 2000px;
  box-sizing: border-box;
  padding: 200px 0 0 9%;
}

.container li {
  float: left;
  margin: 0 50px 50px 0;
}

.container li a {
  display: block;
  width: 350px;
  height: 400px;
  border: 1px solid skyblue;
  border-radius: 10px;
  position: relative;
  color: #000;
  transition: all 0.3s;
}

.container li a:hover {
  box-shadow: 0 0 10px skyblue;
}

.common_content {
  position: absolute;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  line-height: 2em;
}

.common_img img {
  width: 100%;
  height: 250px;
  border-radius: 10px 10px 0 0;
}

.common_name {
  font-size: 20px;
  font-weight: bold;
}

.common_area {}

.common_price {}

.heart {
  font-size: 20px;
  position: absolute;
  top: 10px;
  right: 10px;
  color: red;
}

.good {
  font-size: 18px;
  border: 0;
  background: none;
  position: absolute;
  bottom: 20px;
  right: 45px;
  cursor: pointer;
  transition: all 0.3s;
}

.good:hover {
  transform: scale(1.2);
}

.goodNumber {
  position: absolute;
  bottom: 20px;
  right: 20px;
}



/* footer */
.footer {
  width: 100%;
  height: 80px;
  background: linear-gradient(rgb(255, 241, 223), rgb(255, 255, 255));
  position: fixed;
  bottom: 0;
}

.footer_box {
  width: 100%;
  max-width: 1400px;
  height: 100%;
  position: relative;
  margin: 0 auto;
}

.foot_menu {
  display: flex;
  width: 50%;
  justify-content: space-around;
  margin-top: 10px;
}

.foot_menu li {}

.foot_menu li a {
  color: #888;
  font-size: 14px;
}

.foot_menu li a:hover {
  color: #222;
}